<template>
	<div class="edit-link">
		Shorten link: <input type="text" v-model="shortenLink" v-on:keydown="onType"><br>
		Full link: <input type="text" v-model="fullLink"><br>
		<button v-on:click="findLink">find</button>
		<button v-on:click="saveLink">save</button>
	</div>
</template>

<script>
import firebaseApp from './firebaseInit'
let db = firebaseApp.firestore();
db.settings({timestampsInSnapshots: true})
		
export default {
	name: 'EditLink',
	data: function(){
		return {
			shortenLink: '',
			fullLink: '',
		}
	},
	methods: {
		onType: function(){
			// console.log('typing...');
		},
		findLink: function(){
			db.collection("shortenURL").doc(this.shortenLink).get().then(doc => {
				this.fullLink = doc.data().url;
			});
		},
		saveLink: function(){
			db.collection("shortenURL").doc(this.shortenLink).set({
				url: this.fullLink
			})
		}
	},
	created: function(){
	}
}
</script>



