<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <h2>Essential Links</h2>
    <ul>
			<li v-for="person in contacts" :key="person.name">{{person.slug}}:{{ person.name }}</li>
    </ul>
		<button v-on:click="logout">LogOut</button>
  </div>
</template>

<script>
import firebase from 'firebase'
import firebaseApp from '@/components/firebaseInit'

export default {
  name: "HelloWorld",
  data() {
    return {
			msg: "Welcome to Your Vue.js App",
			contacts: [],
    };
	},
	methods: {
		logout: function (){
			firebase.auth().signOut().then(() => {
				this.$router.replace('login')
			});
		}
	},
	created: function() {
			let db = firebaseApp.firestore();
			db.settings({timestampsInSnapshots: true});
			db.collection("shortenURL").get().then(query => {
				query.forEach(doc => {
					this.contacts.push({slug: doc.id, name: doc.data().url});
				});
			});
			console.log(this.contacts)
	}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



